@keyframes back{
  from {
    background-color: rgb(41, 38, 38);
  }
  80% {
    background-color: rgb(70, 64, 64);
  }
  70% {
    background-color:  rgb(175, 146, 146);
  }
  60% {
    background-color: rgb(245, 234, 234);
  }
  50% {
    background-color:rgb(136, 80, 80)(158, 50, 50);
  }
  40% {
    background-color:  rgb(138, 68, 68)(158, 50, 50);
  }
  30% {
    background-color:  rgb(161, 49, 49)(158, 50, 50);
  }
  20% {
    background-color: rgb(184, 53, 53)(158, 50, 50);
  }
  10% {
    background-color:  rgb(224, 30, 30)(158, 50, 50);
  }
 
}

body {
  margin: 20px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(41, 38, 38);
  animation-duration: 50s;
  animation-name: back;
  animation-iteration-count: infinite;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
