.main-block{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.main-block__guess-container__score{
  display: flex;
  width: 100%;
  align-items: flex-end;
  justify-content: flex-end;
}

.main-block__guess-container__score__content{
  display: flex;
  align-items: center;
  justify-content: center;
  color: yellow;
  font-family: Cuprum, Arial, Helvetica, sans-serif;
  font-size: 20px;
}

.main-block__header{
  display: flex;
  width: 100%;
}

.main-block__header__logo{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.main-block__header__logo h1{
  font-family: Cuprum, Arial, Helvetica, sans-serif;
  font-size: 60px;
}

.main-block__header__logo h1 span{
  color: yellow;
}

.main-block__guess-container__new-game{
  padding-top: 10px;
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}

.main-block__guess-container__new-game__btn{
  display: flex;
  color: black;
  font-weight: 700;
  text-decoration: none;
  text-align: center;
  user-select: none;
  outline: none;
  border: 2px solid;
  border-radius: 1px;
  transition: 0.2s;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  cursor: pointer;
}

.main-block__guess-container__new-game__btn:hover{
  background: rgba(255,255,255,.2);
  cursor: pointer;
}

.main-block__guess-container__new-game__btn:active{
  background: yellow;
  cursor: pointer;
}

.main-block__guess-container__new-game__btn button{
  align-items: flex-start;
  justify-content: flex-start;
  cursor: pointer;
}

.main-block__guess-container{
  display: flex;
  width: 80%;
  height: 100%;
  flex-direction: column; 
  align-items: center;
  justify-content: space-between;
}

.main-block__guess__player{
  padding-top: 10px;
  width: 100%;
}

audio{
  background-color: rgb(81, 179, 42);
  width: 98%;
  display: flex;
  color: black;
  font-weight: 700;
  text-decoration: none;
  text-align: center;
  user-select: none;
  outline: none;
  border: 2px solid;
  border-radius: 1px;
  transition: 0.2s;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
}

.main-block__guess-container__block{
  display: flex;
  width: 100%;
}

.main-block__guess-container__block__wrapper{
  display: flex;
  flex-direction: column;
  width: 100%;

}

.main-block__guess-container__block__wrapper img{
  width: 300px;
  height: 300px;
  color: black;
  font-weight: 700;
  text-decoration: none;
  text-align: center;
  user-select: none;
  outline: none;
  border: 2px solid;
  border-radius: 1px;
  transition: 0.2s;
  box-shadow: 0 0 10px yellow;
}

.main-block__guess-container__block__wrapper__section{
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 350px;
  margin-top: 60px;
  background-color: rgb(81, 179, 42);
  font-family: Cuprum, Arial, Helvetica, sans-serif;
  color: black;
  font-weight: 700;
  text-decoration: none;
  text-align: center;
  user-select: none;
  outline: none;
  border: 2px solid;
  border-radius: 1px;
  transition: 0.2s;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
}

.main-block__guess-container__block__wrapper__section__info{
  display: flex;
  flex-direction: column;
  padding-left: 20px;
}

.main-block__guess-container__block__wrapper__section__info h2{
  font-family: Cuprum, Arial, Helvetica, sans-serif;
  font-size: 55px;
  margin: 0;
}

.main-block__guess-container__block__wrapper__section__info h3{
  font-family: Cuprum, Arial, Helvetica, sans-serif;
  font-size: 30px;
  margin: 0;
  padding-top: 7px;
}

.main-block__guess-container__block__wrapper__section__info p{
  font-family: Cuprum, Arial, Helvetica, sans-serif;
  font-size: 20px;
  margin: 0;
  padding-top: 5px;
}

.main-block__guess-container__block__wrapper__put-place{
  display: inline-flex;
  width: 98.5%;
  height: 350px;
  align-items: center;
  justify-content: space-between;
  margin-top: 60px;
  background-color: rgb(207, 196, 185);
  font-family: Cuprum, Arial, Helvetica, sans-serif;
  font-weight: 700;
  text-decoration: none;
  text-align: center;
  user-select: none;
  outline: none;
  border: 2px solid;
  border-radius: 1px;
  transition: 0.2s;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  cursor: pointer;
}

.main-block__guess-container__block__wrapper__put-place h2{
  font-size: 40px;
  padding-left: 20px;
}

.main-block__guess-container__block__wrapper__put-place__content{
  display: inline-flex;
  width: 100%;
  padding-left: 20px;
}

.main-block__guess-container__block__wrapper__put-place__content-text-audio{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding-left: 10px;
}

.main-block__guess-container__block__wrapper__put-place__content-text-audio h2{
  font-family: Cuprum, Arial, Helvetica, sans-serif;
  font-size: 35px;
  margin: 0;
}

.main-block__guess-container__block__wrapper__put-place__content-text-audio h3{
  font-family: Cuprum, Arial, Helvetica, sans-serif;
  font-size: 30px;
  margin: 0;
  padding-top: 7px;
}

.main-block__guess-container__block__wrapper__put-place__content-text-audio p{
  font-family: Cuprum, Arial, Helvetica, sans-serif;
  font-size: 20px;
  margin: 0;
  padding-top: 5px;
}

.main-block__guess-container__block__wrapper__put-place__content-text-audio audio{
  width: 50%;
  margin-bottom: 5px;
}


.main-block__guess-container__block__wrapper__next-button{
  width: 20%;
  position: fixed;
  height: 60px;
  background-color: yellow;
  font-family: Cuprum, Arial, Helvetica, sans-serif;
  font-size: 35px;
  color: black;
  font-weight: 700;
  text-decoration: none;
  text-align: center;
  user-select: none;
  outline: none;
  border: 2px solid;
  border-radius: 1px;
  transition: 0.2s;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  cursor: pointer;
  align-items: center;
  justify-content: center;
  transform: translate(-60%, 400%);
  z-index: 5;
}

.main-block__guess-container__block__wrapper__next-button:hover{
  background: green;
  color: aliceblue;
  cursor: pointer;
}

.main-block__guess-container__block__wrapper__next-button:active{
  background: yellow;
  cursor: pointer;
}


.main-block__guess-container__block__wrapper__put-place__buttons__a, .main-block__guess-container__block__wrapper__put-place__buttons__b,
.main-block__guess-container__block__wrapper__put-place__buttons__c, .main-block__guess-container__block__wrapper__put-place__buttons__d,
.main-block__guess-container__block__wrapper__put-place__buttons__e, .main-block__guess-container__block__wrapper__put-place__buttons__f {
  width: 100%;
  display: flex;
}

.main-block__guess-container__block__wrapper__put-place__buttons__a button, .main-block__guess-container__block__wrapper__put-place__buttons__b button,
.main-block__guess-container__block__wrapper__put-place__buttons__c button, .main-block__guess-container__block__wrapper__put-place__buttons__d button,
.main-block__guess-container__block__wrapper__put-place__buttons__e button, .main-block__guess-container__block__wrapper__put-place__buttons__f button{
  display: flex;
  width: 100%;
  height: 45px;
  margin-bottom: 5px;
  font-family: Cuprum, Arial, Helvetica, sans-serif;
  color: black;
  font-weight: 700;
  text-decoration: none;
  text-align: center;
  user-select: none;
  outline: none;
  border: 2px solid;
  border-radius: 1px;
  transition: 0.2s;
  padding: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.5);
  cursor: pointer;
  margin-right: 10%;
  align-items: center;
  justify-content: center;
  animation-duration: 50s;
  animation-name: look;
}
